export default {
	data() {
		return {
			username:'',
			password:''
		}
	},
	methods: {
		usernametxt(e){
			this.username = e.detail.value;
			
		},
		passwordtxt(e){
			this.password = e.detail.value;
		},
		register(){
			uni.request({
				url: 'http://192.168.0.105:9000/users/register',
				method: 'POST',
				data: {
					username:this.username,
					password:this.password
				},
				header:{
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
